.navbar {
    position: fixed;
    top: 25px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
    z-index: 1;
}
.navbar__list{
    margin: 0;
}
.navbar__list__link{
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
    background-color: white;
    padding: 10px 20px;
    font-weight: 700;
    border-radius: 5px;
}
.navbar__list__link:hover {
    position: relative;
    color: white;
    background-color: gray;
}
.navbar__list__link:not(:last-child) {
    margin-bottom: 1px;
}